.scrollup{
    position: fixed;
    right: 2.5rem;
    bottom: -20%;
    background-color: #FF2C55 ;
    opacity: 0.8rem;
    padding: 0.3rem 0.5rem;
    border-radius: 0.4rem;
    z-index: var(--z-tooltip);
    transition: .4s;
}

.scrollup__icon {
    font-size: 1.5rem;
    color: black;
}

.scrollup:hover {
    background-color: #FFC857;
}

/* Show scroll */
.show-scroll{
    bottom: 3rem;
}


/*=============== BREAKPOINTS ===============*/
/* For large devices */
@media screen and (max-width: 992px) {
    .scrollup {
        right: 1.5rem;
        padding: 0.25rem 0.4rem;
    }

    .scrollup__icon {
        font-size: 1.25rem;
    }
  }
  
  /* For medium devices */
  @media screen and (max-width: 768px) {
       .show-scroll {
        bottom: 4rem;
       }
  }
  
  /* For small devices */
  @media screen and (max-width: 350px) {
    .scrollup {
        right: 1rem;
    }
  }
  
